.toggle-button {
    height: 24px;
    width: 30px;
    display: none;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
}

.toggle-button:focus {
    outline: none;
}


.line {
    background: hsl(350, 100%, 62%);
    height: 2px;
    width: 30px;
}

@media only screen and (max-width: 812px) {
    .toggle-button {
        display: -ms-flexbox;
        display: flex;
    }
}



