/*-- Resest Css --*/
* {
  padding: 0px;
  margin: 0px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: "Lato", sans-serif;
}

html,
body {
  max-width: 100%;
  overflow-x: hidden;
  height: 100%;
}

/*-- Nav Css --*/
.toggle-button {
    display: none;
}

/*-- About Css --*/
section {
    padding: 2rem 0;
}
#about {
  padding: 0.5rem;
}

.title,
.titleDesc {
  text-align: center;
  padding: 1rem;
  max-width: 900px;
  margin: 0 auto;
}

.title h2 {
  color: hsl(350, 100%, 62%);
}

.titleDesc span {
  color: hsl(350, 100%, 62%);
}

.col {
  max-width: 1200px;
  margin: 10px auto;
  display: -ms-flexbox;
  display: flex;
}

.col.col-left {
  width: 50%;
  float: left;
  text-align: left;
  padding-right: 20px;
}

.col.col-right {
  width: 50%;
  float: right;
  text-align: left;
  padding-left: 20px;
}

/*-- Work Css --*/
#work-logos {
  display: -ms-flexbox;
  display: flex;
  margin: 0.5rem auto;
  max-width: 1200px;
  -ms-flex-pack: distribute;
  justify-content: space-around;
  -ms-flex-flow: row wrap;
  flex-flow: row wrap;
  -ms-flex-align: stretch;
  align-items: stretch;
}

#work-logos .work-logo {
  padding: 0px 10px;
  display: block;
  margin: auto;
}

/*-- Slider Css --*/
.slick-slider {
  margin: 0px auto 1rem;
}

.slick-slide img {
  display: block;
  margin: 0px auto;
  width: 100%;
  height: 500px;
}

/*-- Skills Css --*/
#skills {
  display: -ms-flexbox;
  display: flex;
  margin: 0.5rem auto;
  max-width: 1200px;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-align: stretch;
      align-items: stretch;
}

.skillBox {
  display: block;
  margin: 0.5rem auto;
  width: 350px;
  border: solid 1px #e5e5e5;
  padding: 15px;
  border-radius: 3px;
  -webkit-box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
          box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  line-height: 30px;
}

.skillBox span {
  padding: 5px;
  border: solid 1px #e5e5e5;
  border-radius: 3px;
  width: 100%;
  cursor: pointer;
}

.skillBox span:hover {
  color: hsl(350, 100%, 62%);
}

.skillBox img {
  height: 250px;
  width: 100%;
}

/*-- Footer Css --*/
#footer {
  background: #222;
  height: auto;
  padding: 10px;
}

.footer-container {
  max-width: 1200px;
  display: -ms-flexbox;
  display: flex;
  margin: 20px auto;
}

.footer-logo {
  color: #e5e5e5;
  width: 33%;
}

.footer-logo h2 {
  font-family: "Pacifico", cursive;
  font-size: 30px;
  color: hsl(350, 100%, 62%);
}

.footer-contact {
  width: 33%;
  color: #e5e5e5;
  margin: 5px auto;
}

.footer-contact h3 {
  color: hsl(350, 100%, 62%);
}

.footer-contact i {
  font-size: 18px !important;
  pointer-events: none;
}

.footer-contact i:hover {
  color: #e5e5e5 !important;
}

.footer-contact a {
  color: #e5e5e5;
  text-decoration: none;
}

.footer-social {
  margin: 5px auto;
}

.footer-social h3 {
  color: hsl(350, 100%, 62%);
  font-size: 18px;
}

#footer i {
  color: #e5e5e5;
  font-size: 30px;
  cursor: pointer;
  margin: 0px 10px 0px 0px;
}

#footer i:hover {
  color: hsl(350, 100%, 62%);
}

/*-- Media Queries --*/
@media only screen and (max-width: 812px) {

  .navItems ul li {
    display: none;
  }
    


  .footer-container {
    display: block;
  }

  .footer-logo {
    color: #e5e5e5;
    width: 100%;
    text-align: center;
  }

  .footer-logo h2 {
    font-size: 35px;
  }

  .footer-contact {
    width: 100%;
    color: #e5e5e5;
    margin: 15px auto;
    text-align: center;
  }

  .footer-social {
    margin: 5px 0px;
    text-align: center;
  }

  .footer-social i {
    margin: 0px 15px;
  }
}

@media only screen and (max-width: 812px) {
    .col {
      display: block;
    }
    
    .col.col-left, .col.col-right {
        width: 100%;
        float: none;
        padding: 0.5rem;
    }
    
    .work-logo:first-child {
        margin: 3rem auto !important; 
    }
    
   
 .skillBox {
       margin: 0.5rem auto;
    }
}



